import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

interface MyPageButtonProps {
  isToken: boolean;
}

const MyPageButton: React.FC<MyPageButtonProps> = ({ isToken }) => {
  return (
    <DivMainPost>
      {isToken ? (
        <>
          <LinkPage to="/logout">  </LinkPage>
          <LinkPage to="/mypage/0">  </LinkPage>
          <LinkPage to="/myposts">   </LinkPage>
        </>
      ) : (
        <>
          <LinkPage to="/login">  </LinkPage>
          <LinkPage to="/signup">  </LinkPage>
          <LinkPage to="/find-email"> </LinkPage>
        </>
      )}
    </DivMainPost>
  );
};

const DivMainPost = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const LinkPage = styled(Link)`
  margin: 10px;
`;

export default MyPageButton;
